.header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  padding: 32px 72px;
  background: #0f132d;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 10;

  &__navigation {
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }

  .socials {
    display: flex;
    align-items: center;
    margin-right: 120px;

    li {

      &+li {
        margin-left: 16px;
      }
    }
  }

  .icon {
    display: block;
    width: 26px;
    height: 24px;
    background: url(../images/sprite.png) no-repeat;



    &.facebook {
      background-position: 0 0;
      width: 10px;
    }

    &.twitter {
      background-position: -28px -2px;
      width: 20px;
    }

    &.linkedin {
      background-position: -68px 1px;
      width: 20px;
    }

    &.instagram {
      background-position: -105px -1px;
      width: 20px;
    }

    &.youtube {
      background-position: -145px 0;
      width: 25px;
    }

    &.account {
      background-position: -295px 0;
      position: relative;

    }

    &.search {
      background-position: -368px 0;
      margin-left: 45px;
    }
  }

  &__logo {
    color: $white-min;
    font-weight: 700;
    font-size: 25px;
    margin-right: 40px;
  }

  .account {

    &__menu {
      position: absolute;
      top: 100%;
      left: 50%;
      transform: translateX(-50%);
      padding: 30px 0 0;
      height: 0;
      transition: 0.3s;
      overflow: hidden;

      &:hover {
        overflow: initial;
        transition: .5s;
      }

      a {
        display: block;
        color: $white;
        font-size: 20px;
        padding: 10px 25px;
        white-space: nowrap;
        background: blue;

        &:first-child {
          border-radius: 5px 5px 0 0;
        }

        &:last-child {
          border-radius: 0 0 5px 5px;
        }

      }
    }
  }
}